.room-code {
    height: 40px;
    border-radius: 8px;
    overflow: hidden;

    background: #fff;
    border: 2px solid #835afd;
    cursor: pointer;

    display: flex;

    div {
        background: #835afd;
        padding: 8px;
        border: 1px solid #835afd;;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    span {

        display: block;
        align-self: center;
        flex: 1;
        padding: 0 16px 0 12px;
        width: 230px;
        font-size: 1.4rem;
        font-weight: 500;
    }
}